.extension {
  padding: 4rem 2rem;
  text-align: center;
}

.extension-container {
  max-width: 1200px;
  margin: 0 auto;
}

.extension-header {
  max-width: 600px;
  margin: 0 auto 3rem;
}

.extension-header h2 {
  font-size: 2rem;
  color: var(--clr-dark-blue);
  margin-bottom: 1rem;
}

.extension-header p {
  color: var(--clr-gray);
  line-height: 1.5;
}

.extension-cards {
  display: flex;
  justify-content: center;
  gap: 2rem;
  flex-wrap: wrap;
  align-items: flex-start;
}

.browser-card {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  max-width: 280px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.browser-card p {
  color: var(--clr-gray);
  margin-bottom: 2rem;
  padding-bottom: 2rem;
}

.browser-card::after {
  content: '';
  position: absolute;
  bottom: 6rem;
  left: 0;
  width: 100%;
  height: 4px;
  background: url('../../assets/images/bg-dots.svg') repeat-x center;
}

.browser-card:nth-child(1) {
  margin-top: 0;
}

.browser-card:nth-child(2) {
  margin-top: 2rem;
}

.browser-card:nth-child(3) {
  margin-top: 4rem;
}

.browser-card img {
  width: 100px;
  height: 100px;
  margin-bottom: 2rem;
}

.browser-card h3 {
  color: var(--clr-dark-blue);
  margin-bottom: 0.5rem;
}

.browser-card p {
  color: var(--clr-gray);
  margin-bottom: 2rem;
}